import React, { useState } from "react";
import { Form, FormGroup, Label, Input, Button, UncontrolledAlert } from "reactstrap";
import { Link } from "react-router-dom";

const SignIn = (props) => {

    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')

    const handleChange = (e) => {
        const target = e.target
        return (target.name === 'email') ? setEmail(target.value) : setPassword(target.value)
    }

    const handleSubmit = (event) => {
        event.preventDefault()
        if (!props.isLoading) {
            props.onSubmitForm(email, password)
        }

    }
    
    const error = props.errMsg
    return (
        <div className="col-sm-6 offset-sm-3 col-md-4 offset-md-4 text-left mt-5">
            <h1>Sign In</h1>
            {error && (
            <UncontrolledAlert color="danger" className="error">
                {error}
            </UncontrolledAlert>)}
            <p>First Time here? <Link to="/signup">Create an account</Link></p>
            <Form onSubmit={handleSubmit}>
                <FormGroup>
                    <Label for="email">Email</Label>
                    <Input
                        type="email"
                        name="email"
                        id="email"
                        placeholder="Enter email"
                        value={email}
                        onChange={handleChange}
                        required
                    />
                </FormGroup>
                <FormGroup>
                    <Label for="password">Password</Label>
                    <Input
                        type="password"
                        name="password"
                        id="password"
                        placeholder="Enter password"
                        value={password}
                        onChange={handleChange}
                        required
                    />
                </FormGroup>
                <Button type="submit" disabled={props.isLoading}>
                    {props.isLoading ? 'Please wait ...' : 'Submit'}
                </Button>
            </Form>
        </div>
    )
    
}

export default SignIn